.gemini {
    text-align: center;
  }
  
  .gemini-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .gemini-logo {
      animation: gemini-logo-spin infinite 20s linear;
    }
  }
  
  .gemini-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: rgb(255, 255, 255);
  }
  
  .gemini-link {
    color: #ffffff;
  }
  
  @keyframes gemini-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  
 